          // {"name": "新闻动态", "path": "/news"},

          {"name": "关于我们", "path": "/ours"}
        ],
      }
    },
    methods: {
      getScrollTop(){
        var scrollTop=0;
        if(document.documentElement && document.documentElement.scrollTop){
          scrollTop=document.documentElement.scrollTop;
        }else if(document.body){
          scrollTop=document.body.scrollTop;
        }
        return scrollTop;
      }
    },
    mounted() {
      let that =this
      $('.mouse_show').on('mouseenter',function () {
        $('.tst_top').css({'background':'#111c2c'})
        $('.list_chanpin').show()
      })
      $('.mouse_hide').on('mouseenter',function () {
        $('.tst_top').css({'background':''})
        $('.list_chanpin').hide()
      })
      $('.list_chanpin').mouseleave(function () {
        $('.tst_top').css({'background':''})
        $('.list_chanpin').hide()
      })
      window.onscroll=function(){
        let headerHeight = that.getScrollTop()
        if(headerHeight>=700) {
          //242a37
          $('.tst_top').css({'background':'#111c2c'})
        } else {
          $('.tst_top').css({'background':''})
        }
      }

    },
    created(){

    }
  }
</script>

