// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import ElementUI from 'element-ui'
import '../static/css/reset.css'

import $ from 'jquery'
import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min.js'
import 'element-ui/lib/theme-chalk/index.css'
import '../static/css/BreakingNews.css'
import '../static/js/BreakingNews.js'
import '../static/css/jquery.bxslider.css'
import '../static/js/jquery.bxslider.js'


Vue.use(VueRouter)
Vue.use(VueResource)
Vue.use(ElementUI)
// Vue.use(VueAwesomeSwiper)
Vue.config.productionTip = false

import Home from './components/Home.vue'
import About from './components/about/About.vue'
import AboutSimSuite from './components/about/AboutSimSuite.vue'
import AboutMilitaryIcon from './components/about/AboutMilitaryIcon.vue'
import AboutDataTraffic from './components/about/AboutDataTraffic.vue'


import Program from './components/program/Program.vue'
import News from './components/news/News.vue'
import Works from './components/works/Works.vue'
import Ours from './components/ours/Ours.vue'
import OursCall from './components/ours/Ours_call' //联系我们
import OursZiZhi from './components/ours/Ours_zizhi' //资质


const routes = [
  {path: '/', redirect: '/home'},
  {path: '/home', component: Home},
  {path: '/about', component: About},
  {path: '/aboutSimSuite', component: AboutSimSuite}, //GT-SimSuite
  {path: '/aboutMilitaryIcon', component: AboutMilitaryIcon}, //二三维通用军用标图系统
  {path: '/aboutDataTraffic', component: AboutDataTraffic}, //数据通信



  {path: '/OursZiZhi', component: OursZiZhi},
  {path: '/program', component: Program},
  {path: '/works', component: Works},
  {path: '/news', component: News},
  {path: '/ours', component: Ours},
  {path: '/OursCall', component: OursCall},
  {path: '/OursZiZhi', component: OursZiZhi},
]

const router = new VueRouter({
  linkActiveClass: 'active',
  routes
})

// 跳转后返回顶部
router.afterEach((to,from,next) => {
  clearInterval(this.timer)
  $('.loading').show()
  this.timer = setInterval(function () {
    $('.loading').hide()
  },500)
  window.scrollTo(0,0);
  var strPath = to.path
  if (strPath.substring(0, 4) == '/abo') {
    $(".list_chanpin").hide();
  }
})

/* eslint-disable no-new */
let vm = new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



