<template>
  <div class="tst">
    <div class="loading"><i class="el-icon-loading"></i></div>
    <v-header></v-header>
    <router-view/>
    <v-footer></v-footer>
  </div>

</template>

<script>
  import header from './components/header/header.vue'
  import footer from './components/footer/Footer.vue'

  export default {
    data() {
      return {

      }
    },
    methods: {},
    components: {
      'v-header': header,
      'v-footer':footer
    },
    created() {

    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  html,body,p,h1,h2,h3,h4,h5,h6,span,i {
    font-family "微软雅黑"
  }
  .tst
    width 100%
    height 100%
    .loading
      width 100%
      height 100%
      background rgba(0,0,0,.4)
      position fixed
      display none
      z-index 99999
      i
        position absolute
        font-size 30px
        left 50%
        top 50%

</style>



